/* CSS Normalize */

article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button], input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

/* clearfix */
.clearfix:after {visibility: hidden; display: block; font-size: 0; content: " "; clear: both; height: 0; }

/* Sane border box */
* { box-sizing: border-box; }
html { font-size: 100%; }


:root {

    /* Colors */

    /* Blues */
    --middle-blue: rgba(29, 102, 150, 1);
    --deep-blue: rgba(28, 73, 105, 1);
    --dirty-blue: rgba(28, 73, 105, 0.8);
    --shadow-blue-opaque: rgba(58, 133, 182, 1);
    --shadow-blue-transparent: rgba(58, 133, 182, 0.24);
    --light-desaturated-blue: rgba(204, 235, 255, 1);
    --dark-desaturated-blue: rgba(109, 152, 181, 1);

    /* Pinks */
    --soft-pink: rgba(255, 198, 197, 1);
    --white-pink: rgba(255, 235, 235, 1);
    --soft-pink-transparent-shadow: rgba(255, 198, 197, 0.8);
    --soft-pink-very-transparent-shadow: rgba(255, 198, 197, 0.24);

    --warm-white: rgb(252, 252, 250);    

    --cheesy-orange: rgba(251, 211, 169, 1);    

    --disabled-grey: #ededed;

    /* Z-indexes */
    --modal-z-index: 1000000;
    --appbar-z-index: 100000;
    --fixed-top-sheet-z-index: 10000;

    /* transitions */
    --ease-out-quintic: cubic-bezier(0.23, 1, 0.320, 1);
    --ease-in-quintic: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    --ease-in-exponential: cubic-bezier(0.95, 0.05, 0.795, 0.035);

    /* patterns */
    --big-blue-dots: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAMElEQVQYlWN4/OXXf2IwA+0V/vnz5/+fP3/wK2RgYGCAKURXzIDNNIIKibaavsEDAA4HewGKI48SAAAAAElFTkSuQmCC);
    --small-blue-dots: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAGElEQVQYlWNgoCl4/OXXf+oqHESAtp4DAOCcD0FnrYGRAAAAAElFTkSuQmCC);
    --pink-dots: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAIElEQVQYlWP4f+zof2IwAx0U/v9PhEJkQB0TB9jXODAAKC1j03LYSdgAAAAASUVORK5CYII=);
}

body {
    margin: 0;
    padding: 0;
    font-family: Poppins,-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1.6;
    color: rgba(29, 102, 150, 1);
    color: var(--middle-blue);

    background-image: -webkit-linear-gradient(top, rgba(255,255,255,0.75), white), url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAIElEQVQYlWP4f+zof2IwAx0U/v9PhEJkQB0TB9jXODAAKC1j03LYSdgAAAAASUVORK5CYII=);

    background-image: linear-gradient(to bottom, rgba(255,255,255,0.75), white), url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAIElEQVQYlWP4f+zof2IwAx0U/v9PhEJkQB0TB9jXODAAKC1j03LYSdgAAAAASUVORK5CYII=);

    background-image: -webkit-linear-gradient(top, rgba(255,255,255,0.75), white), var(--pink-dots);

    background-image: linear-gradient(to bottom, rgba(255,255,255,0.75), white), var(--pink-dots);
    background-repeat: repeat;
    background-attachment: fixed;
}
.container {
    margin-right: auto;
    margin-left: auto;
    padding-left: 1rem;
    padding-right: 1rem;
    width: 100vw;
}

/* Screens larger than 360px get a larger padding */
@media (min-width: 360px) {
    .container {
        padding-left: 1.75rem;
        padding-right: 1.75rem;
    }
}

/* Between 480px and 767 (small tablets), fix the width to 480px */
@media (min-width: 480px) and (max-width: 767px) {
    .container {
        width: 480px;
    }
}

/* Greater than 768px (tablets and greater) */
@media (min-width: 768px) {
    .container {
        width: 680px;
    }
}

/* Only to be used inside .container to cancel out the padding */
.row {
    margin-left: -1rem;
    margin-right: -1rem;
}
@media (min-width: 360px) {
    .row {
        margin-left: -.75rem;
        margin-right: -.75rem;
    }
}



