.Appbar-header {
    background: var(--warm-white);   
    position: fixed;
    top: 0;
    width: 100vw;
    border-radius: 0 0 1rem 1rem;
    border-bottom: 1px solid var(--soft-pink);
    box-shadow: 0 0 4px 0 var(--soft-pink-very-transparent-shadow), 0 0 4px 4px rgba(255, 198, 197, 0.12);
    z-index: var(--appbar-z-index);
}

.Appbar-header__content {
    margin: 0 auto;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.btn {
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
    height: 2rem;
    border-radius: 1rem;
    padding-left: 1rem;
    padding-right: 1rem;
    border: 1px solid var(--deep-blue);
    color: var(--deep-blue);
}

.btn-default {
    background-color: var(--soft-pink);
}

.btn-primary {
    background-color: var(--cheesy-orange);
}
.section-divider {
    width: 100px;
    height: 8px;
    background-color: var(--soft-pink);
    border: 1px solid var(--middle-blue);
    border-radius: 4px;
}

.section-divider-no-outline {
    width: 100px;
    height: 8px;
    background-color: var(--soft-pink);
    border-radius: 4px;
}
.HelpInfo {
    height: 100%;
    max-width: 360px;
    margin: 0 auto;
}

.HelpInfo__greeting {
    font-family: Lobster, cursive;
    font-size: 4rem;
    line-height: 1.3;
    margin:0;
    padding-top: 3rem;
    padding-bottom: 2rem;
}

.HelpInfo__lead {
    padding-top: 2rem;
    margin: 0;
    font-weight: 600;
    font-size: 1.5rem;
}

.HelpInfo__contact-container {
    margin: 2.25rem 0;
}

.HelpInfo__contact-number {
    font-weight: 600;
    font-size: 2.25rem;
    color: var(--middle-blue);
}


.ImageButton {
    display: flex;
    align-items: center;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    width: 100%;
    background-color: var(--white-pink);
    border: 1px solid var(--shadow-blue-opaque);
    border-radius: 1rem;
