    background-repeat: no-repeat;
    background-attachment: fixed;    
    padding-top: 4rem;
    background-position: right calc(100% - calc((123/360) * 100vw)) top 4rem;
}

@media screen and (min-width:480px) {
    .MainContainer {
        background-size: 535px;
        background-position: left -132px bottom -75px;
    }
}

.MainContainer__container {
    height: 100%;
    overflow: auto;
}



.fade-appear {
    opacity: 0;
}
.fade-appear.fade-appear-active {
    opacity: 1;
    -webkit-transition: opacity 500ms ease-out;
    transition: opacity 500ms ease-out;
}

.slide-up-appear {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
}
.slide-up-appear.slide-up-appear-active {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    -webkit-transition: -webkit-transform 350ms var(--ease-out-quintic);
    transition: -webkit-transform 350ms var(--ease-out-quintic);
    transition: transform 350ms var(--ease-out-quintic);
    transition: transform 350ms var(--ease-out-quintic), -webkit-transform 350ms var(--ease-out-quintic);
}

.pop-appear {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);    
}
.pop-appear.pop-appear-active {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);    
    -webkit-transition: opacity 250ms var(--ease-out-quintic), -webkit-transform 250ms var(--ease-out-quintic);    
    transition: opacity 250ms var(--ease-out-quintic), -webkit-transform 250ms var(--ease-out-quintic);    
    transition: opacity 250ms var(--ease-out-quintic), transform 250ms var(--ease-out-quintic);    
    transition: opacity 250ms var(--ease-out-quintic), transform 250ms var(--ease-out-quintic), -webkit-transform 250ms var(--ease-out-quintic);
}
.duplex-greeting-grid {
    height: 100%;
    max-width: 360px;
    margin: auto auto;
    display: grid;
    grid-template-rows: auto 0.5rem auto;
    grid-template-areas: 
        "greeting"
        "section-divider"
        "content";
}

.duplex-greeting-grid__greeting {
    grid-area: greeting;
    align-self: end;
    padding-bottom: 2rem;
    min-height: 40vh;
    display: flex;
    flex-direction: column-reverse;
}

.duplex-greeting-grid__greeting__text {    
    font-family: Lobster, cursive;
    font-size: 4rem;
    line-height: 1.3;
    margin: 0;
}

.duplex-greeting-grid__section-divider {
    grid-area: section-divider;
}

.duplex-greeting-grid__content {
    padding-top: 2rem;
    grid-area: content;
    align-self: start;

    display: grid;
    grid-template-rows: auto auto;
    grid-row-gap: 2rem;   

}

@media (max-width: 359px) {
    .duplex-greeting-grid__content {
        grid-row-gap: 1rem;       
    }
}


.simple-greeting-grid {
    height: 100%;
    max-width: 360px;
    margin: 0 auto;
    display: grid;
    grid-template-rows: auto 0.5rem auto;
    grid-template-areas: 
        "greeting"
        "section-divider"
        "content";
}

.simple-greeting-grid__greeting {
    grid-area: greeting;
    align-self: end;
    padding-bottom: 2rem;
    min-height: 40vh;
    display: flex;
    flex-direction: column-reverse;
}

.simple-greeting-grid__greeting__text {    
    font-family: Lobster, cursive;
    font-size: 4rem;
    line-height: 1.3;
    margin: 0;
}

.simple-greeting-grid__content {
    grid-area: content;
    align-self: start;

    padding-top: 2rem;
}

.simple-greeting-grid__content__lead {
    margin: 0;
    font-weight: 600;
    font-size: 1.5rem;
}

.simple-greeting-grid__content__body {
    font-weight: 500;
    font-size: 1rem;
}
.ContentContainer {
    min-height: inherit;
    display: flex;
    flex-direction: column;
}

.ContentContainer__bottom {
    flex-grow: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
}
/* Icon Text Buttons */

.icon-text-btn {
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
    padding: 0;
    height: 2rem;
    border-radius: 1rem;
    border: 1px solid var(--deep-blue);
    color: var(--deep-blue);
}

.icon-text-btn-large {
    height: 2.5em;
    border-radius: 1.25rem;
}

/* Apply default button colors */

.icon-text-btn-default {
    background-color: var(--soft-pink);
}

.icon-text-btn-primary {
    background-color: var(--cheesy-orange);
}

/* Style the contents: Flexbox */

.icon-text-btn__content {
    display: flex;
    align-items: center;
}

.icon-text-btn__content-icon-right {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
}

/* Style the icon */

.icon-text-btn__content__icon {
    margin-top:-1px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--deep-blue);
    border-radius: 50%;
    height: 2rem;
    width: 2rem;
}

.icon-text-btn__content__icon-large {
    height: 2.5rem;
    width: 2.5rem;
}


/* Style the text */

.icon-text-btn__content__text {
    padding: 0 1.2rem 0 0.5rem;
    margin: 0 auto;
}

.icon-text-btn__content__text-icon-right {
    padding: 0 0.5rem 0 1.2rem;
    margin: 0 auto;
}



.TopSheet {
    margin-top: -1em;
    padding-top: 1em;
    border-radius: 0 0 1rem 1rem;
    border-bottom: 1px solid var(--soft-pink);
    background-color: var(--warm-white);
    box-shadow: 0 2px 2px 0 var(--soft-pink-very-transparent-shadow);
    background-image: var(--small-blue-dots);
    background-repeat: repeat;
}

.TopSheet--fixed {
    position:fixed;
    z-index: var(--fixed-top-sheet-z-index);
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%)
}

.BottomSheet {
    border-radius: 1rem 1rem 0 0;
    border-top: 1px solid var(--soft-pink);
    background-color: var(--warm-white);
    flex-grow: 1;
    padding-bottom: 2rem;
}

/* 480px and greater */
@media (min-width: 480px) {
    .BottomSheet {
        border-left: 1px solid var(--soft-pink);
        border-right: 1px solid var(--soft-pink);
    }
}
