.modal-backdrop {
    /* Hidden by default */
    display: none;
    /* Stay in place */
    position: fixed;
    /* Sit on top */
    z-index: var(--modal-z-index);
    left: 0;
    top: 0;
    /* Full width */
    width: 100%;
    /* Full height */
    height: 100%;
    /* Enable scroll if needed */
    overflow: auto;
    /* Black w/ opacity */
    background-color: rgba(0, 0, 0, 0.6);
}

.modal-backdrop--show {
    display: block;
    -webkit-animation: fadeIn 0.25s ease-out;
            animation: fadeIn 0.25s ease-out;
}

.modal-backdrop--hide {
    display: block;
    -webkit-animation: fadeOut 0.25s ease-out;
            animation: fadeOut 0.25s ease-out;
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

@keyframes fadeIn {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

@-webkit-keyframes fadeOut {
    from {
        opacity: 1
    }
    to {
        opacity: 0
    }
}

@keyframes fadeOut {
    from {
        opacity: 1
    }
    to {
        opacity: 0
    }
}

@-webkit-keyframes slideIn {
    from {
        bottom: -300px;
        /* opacity: 0; */
    }
    to {
        bottom: 0;
        /* opacity: 1; */
    }
}

@keyframes slideIn {
    from {
        bottom: -300px;
        /* opacity: 0; */
    }
    to {
        bottom: 0;
        /* opacity: 1; */
    }
}

@-webkit-keyframes slideOut {
    from {
        bottom: 0;
        opacity: 1;
    }
    to {
        bottom: -300px;
        opacity: 0;
    }
}

@keyframes slideOut {
    from {
        bottom: 0;
        opacity: 1;
    }
    to {
        bottom: -300px;
        opacity: 0;
    }
}

.modal__content {
    border-radius: 1rem 1rem 0 0;
    border-top: 1px solid var(--soft-pink);
    position: fixed;
    bottom: 0;
    background-color: var(--warm-white);
    max-height: calc(100vh - 4rem);
}

.modal__content--show {
    -webkit-animation: slideIn 0.2s ease-out;
            animation: slideIn 0.2s ease-out;    
}

.modal__content--hide {
    -webkit-animation: slideOut 0.2s ease-in;
            animation: slideOut 0.2s ease-in;
}

@media (min-width: 482px) {
