.container {
    margin-right: auto;
    margin-left: auto;
    padding-left: 1rem;
    padding-right: 1rem;
    width: 100vw;
}

/* Screens larger than 360px get a larger padding */
@media (min-width: 360px) {
    .container {
        padding-left: 1.75rem;
        padding-right: 1.75rem;
    }
}

/* Between 480px and 767 (small tablets), fix the width to 480px */
@media (min-width: 480px) and (max-width: 767px) {
    .container {
        width: 480px;
    }
}

/* Greater than 768px (tablets and greater) */
@media (min-width: 768px) {
    .container {
        width: 680px;
    }
}

/* Only to be used inside .container to cancel out the padding */
.row {
    margin-left: -1rem;
    margin-right: -1rem;
}
@media (min-width: 360px) {
    .row {
        margin-left: -.75rem;
        margin-right: -.75rem;
    }
}



.img-responsive {
    display: block;
    max-width: 100%;
    height: auto;
}

